
<template>
    <v-alert color="info" icon="error" dismissible @input="onClose" :value="true">
      {{ text }}
    </v-alert>
</template>

<script>
    export default {
        props: ['text'],
        methods: {
            onClose(){
                this.$emit('dismissed')
            }
        }
    }    
</script>


